
h1,h2{color:#fff;text-align:center;font-size:26px;font-weight:bold;margin:20px 0;text-shadow:2px 2px 4px rgba(0,0,0,0.5);line-height:140%
}
h1.main,h2.main{margin-top:30px
}
h1.small,h2.small{font-size:20px
}
h2{margin-top:40px
}
.page{padding:20px
}
.page p{font-size:18px;line-height:160%;color:#fff
}
.page p i{font-weight:300
}
.page p.center{text-align:center
}
.page p a{text-decoration:none;color:#fff
}
.page__blur{position:fixed;top:0;right:0;bottom:0;left:0;background:rgba(0,0,0,0.7);z-index:1
}
.page__content{position:relative;z-index:2
}
.page img{display:block;max-width:100%;max-height:260px;height:auto;margin:0 auto;padding:10px 0
}
.page ul{margin:0;padding:0;padding-left:15px
}
.page ul li{font-size:18px;line-height:160%;color:#fff;margin-bottom:10px
}
.block{position:fixed;width:inherit;left:0;right:0;top:0;bottom:0;position:fixed;overflow-y:auto;overflow-x:hidden
}
.badge{display:block;font-size:30px;text-align:center;color:#fff;line-height:100%;margin-bottom:-15px
}
.buttons{padding-top:100px
}
.buttons button{background:rgba(0,0,0,0.7);color:#fff;font-size:18px;padding:20px;min-height:90px;-webkit-box-sizing:border-box;box-sizing:border-box;outline:none;border:0;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:100%;margin-bottom:5px;-webkit-transition:background 0.1s ease-in-out;transition:background 0.1s ease-in-out;line-height:140%
}
.buttons button:active{background:#000
}
.buttons--small{padding-top:0px
}
.buttons--small button{padding:15px;min-height:50px
}
a{-webkit-tap-highlight-color:rgba(0,0,0,0)
}
.router-animation-enter-active{-webkit-animation:goOut 0.75s;animation:goOut 0.75s;-webkit-animation-delay:0.5s;animation-delay:0.5s;opacity:0
}
.router-animation-leave-active{-webkit-animation:goIn 0.75s;animation:goIn 0.75s
}
@-webkit-keyframes goIn{
from{-webkit-transform:translateX(0);transform:translateX(0)
}
to{-webkit-transform:translateX(-75px);transform:translateX(-75px);opacity:0
}
}
@keyframes goIn{
from{-webkit-transform:translateX(0);transform:translateX(0)
}
to{-webkit-transform:translateX(-75px);transform:translateX(-75px);opacity:0
}
}
@-webkit-keyframes goOut{
from{-webkit-transform:translateX(-75px);transform:translateX(-75px);opacity:0
}
to{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1
}
}
@keyframes goOut{
from{-webkit-transform:translateX(-75px);transform:translateX(-75px);opacity:0
}
to{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1
}
}
